import styled from 'styled-components';

const LoadingElement = styled.div`
  height: 100%;
`;

const ContainerElement = styled.div`
  height: calc(100% - 64px);
`;

const MapElement = styled.div`
  height: 100%;
`;

export { LoadingElement, ContainerElement, MapElement };



